import { useState, useEffect } from "react";
const axios = require("axios");

export default (userData) => {
  const [myCalcs, setMyCalcs] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://bmtools.arup.com/calcs/api/mechanical/pwadata/${userData.account.userName}`
      )
      .then(function (response) {
        setMyCalcs(response.data.calcs);
      });
  }, []);

  const completeCalc = (calc) => {
    window.open(
      `https://bmtools.arup.com/calcs/mechanical/project/${calc.ProjectCode}`,
      "_blank"
    );
  };

  return {
    myCalcs,
    completeCalc,
  };
};
