import React from "react"
import MicrosoftLogin from "react-microsoft-login";

//Material UI Components
import Button from '@material-ui/core/Button';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Project Components
import MyCalcs from '../my-calcs';

//Styles
import useStyles from './main-hub-styles';

export default function MainHub() {
    const {
        handleNotification,
        loggedIn,
        authHandler,
        userData
    } = useHooks();
    const classes = useStyles();
    return (
        <div>
        {!loggedIn && <MicrosoftLogin clientId="2b4cf606-ac2c-4532-a7b0-483d7f2427cc" authCallback={authHandler} />}
        {loggedIn && 
        <div>
            <h1>Arup Hub</h1>
            <Button variant="contained" onClick={handleNotification}>Send me a Notification</Button>
            <MyCalcs userData={userData} />
        </div>
        }
        </div>
    );
}