import React from "react"

//Material UI Components
import Button from '@material-ui/core/Button';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Styles
import useStyles from './my-calcs-styles.js'

export default function MainHub(props) {
    const {
        userData
    }= props;
    const {
        myCalcs,
        completeCalc
    } = useHooks(userData);
    const classes = useStyles();
    console.log(myCalcs)
    return (
        <div>
            <TableContainer component={Paper} className={classes.table}>
                <Table>
                    <TableHead>
                        <TableCell>Calculation Name</TableCell>
                        <TableCell>Project Name</TableCell>
                        <TableCell>Project Number</TableCell>
                        <TableCell>Stage</TableCell>
                        <TableCell />
                    </TableHead>
                    <TableBody>
                        {myCalcs.map((calc) => (
                         <TableRow key={calc.calculation_id}>
                             <TableCell>{calc.calculation}</TableCell>
                             <TableCell>{calc.JobNameShort}</TableCell>
                             <TableCell>{calc.ProjectCode}</TableCell>
                             <TableCell>{calc.stage}</TableCell>
                             <TableCell><Button variant="contained" onClick={() => completeCalc(calc)}>Complete Calc</Button></TableCell>
                         </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
}